body {
  margin: 0;
  padding: 0;
  font-family: "Roboto", "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 24px;
  color: #283645;
  background-color: #828282;
}
img {
  display: block;
  max-width: 100%;
  height: auto;
}
html {
  height: 100%;
}
body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}
.sticky-footer {
  flex-grow: 1;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page-header {
  width: 1140px;
  margin: 0 auto;
}
.page-header__wrapper {
  background-color: #ffffff;
}
.main-nav {
  background-color: #ffffff;
  margin-bottom: 8px;
}
.main-nav__list {
  margin: 0;
  padding-left: 16px;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
}
.main-nav__item {
  margin-right: 30px;
  padding-top: 15px;
  padding-bottom: 16px;
}
.main-nav__item:hover {
  background-color: #525775;
}
.main-nav__item:active {
  background-color: #272b36;
}
.main-nav__link {
  text-decoration: none;
  text-transform: uppercase;
  font-size: 16px;
  line-height: 19px;
  color: #8d8d8d;
  padding: 0 2px;
}
.breadcrumbs {
  width: 1140px;
  margin: 0 auto;
}
.breadcrumbs__list {
  margin: 0;
  padding-left: 16px;
  display: flex;
  list-style-type: none;
}
.breadcrumbs__item {
  margin-top: 8px;
  padding-bottom: 8px;
  padding-right: 24px;
  position: relative;
}
.breadcrumbs__item:after {
  content: ">";
  width: 14px;
  height: 14px;
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  font-size: 12px;
  line-height: 14px;
}
.breadcrumbs__item:last-child .breadcrumbs__link {
  color: #4f4f4f;
}
.breadcrumbs__item:last-child:after {
  display: none;
}
.breadcrumbs__link {
  text-decoration: none;
  font-size: 12px;
  line-height: 14px;
  color: #2f80ed;
}
.breadcrumbs__link:hover {
  color: #56ccf2;
}
.breadcrumbs__link:visited {
  color: #9b51e0;
}
.page-footer {
  width: 1140px;
  margin: 0 auto;
  background-color: #ffffff;
  padding: 40px 0 0;
  box-sizing: border-box;
  display: grid;
  grid-template-columns: 250px auto 250px;
  column-gap: 20px;
}
.logo {
  padding: 20px 20px;
  margin-bottom: 20px;
  box-sizing: border-box;
  text-decoration: none;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  grid-column: 1/-1;
}
.logo__img {
  width: 50px;
  margin-right: 20px;
}
.logo__title {
  text-transform: uppercase;
  font-weight: 700;
  font-size: 18px;
  line-height: 21px;
  color: #000000;
}
.contacts {
  margin-left: 20px;
  box-sizing: border-box;
}
.contacts__address {
  line-height: 16px;
  font-style: normal;
  color: #4f4f4f;
  padding-left: 28px;
  position: relative;
  margin-bottom: 10px;
}
.contacts__address:before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 21px;
  height: 22px;
  background-image: url(../img/marker.svg);
  background-repeat: no-repeat;
  background-position: center;
}
.contacts__phone {
  padding-left: 28px;
  position: relative;
  margin-bottom: 15px;
  display: block;
  color: #4f4f4f;
  text-decoration: none;
}
.contacts__phone:before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 20px;
  height: 20px;
  background-image: url(../img/phone.svg);
  background-repeat: no-repeat;
  background-position: center;
}
.contacts__social-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}
.contacts__social-item {
  fill: transparent;
  stroke: #828282;
}
.contacts__social-item:hover {
  stroke: #272b36;
}
.about-us__text {
  margin: 0;
  line-height: 16px;
  color: #4f4f4f;
}
.product-page {
  width: 1140px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 270px auto 300px;
  grid-template-rows: repeat(4, auto);
  column-gap: 20px;
  row-gap: 25px;
}
.product-page__title {
  margin: 0;
  font-weight: 700;
  font-size: 22px;
  line-height: 26px;
  color: #333333;
  grid-column: 2 / -1;
}
.filter {
  background-color: #ffffff;
  padding: 16px;
  margin-bottom: 20px;
  grid-row: 2 / 3;
}
.filter__legend,
.filter__results {
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  color: #333333;
}
.filter__legend {
  margin-bottom: 10px;
}
.filter__results {
  padding-bottom: 16px;
  margin-top: 0;
  margin-bottom: 15px;
  border-bottom: 1px solid #bdbdbd;
}
.filter__checkbox,
.filter__radio,
.filter__range,
.filter__keywords {
  display: flex;
  flex-direction: column;
  padding: 0;
  margin: 0;
  margin-bottom: 17px;
  border: none;
}
.filter__range-wrapper {
  display: flex;
  justify-content: space-between;
}
.filter__input {
  height: 35px;
  box-sizing: border-box;
  padding: 0 5px;
  border: 1px solid #bdbdbd;
}
.filter__input--range {
  width: 112px;
}
.filter__button {
  background-color: #56ccf2;
  border: none;
  width: 238px;
  height: 35px;
  font-size: 16px;
  line-height: 19px;
  text-transform: uppercase;
  color: #ffffff;
}
.control {
  position: relative;
  padding-left: 35px;
}
.control:hover .control__mark {
  border-color: #8dcde3;
}
.control:active .control__mark,
.control:focus-within .control__mark {
  border-color: #d22856;
}
.control__mark {
  background: #ffffff;
  border: 1px solid #2d9cdb;
  width: 16px;
  height: 16px;
  position: absolute;
  left: 0;
  top: 0;
}
.control__input[type="checkbox"]:checked + .control__mark {
  background-image: url("../img/checkbox.svg");
  background-repeat: no-repeat;
  background-color: #2d9cdb;
  background-position: center;
}
.control__input[type="radio"] + .control__mark {
  border-radius: 50%;
}
.control__input[type="radio"]:checked + .control__mark::after {
  content: "";
  width: 10px;
  height: 10px;
  background-color: #2d9cdb;
  border-radius: 50%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
}
.control__text {
  font-size: 16px;
  line-height: 19px;
  color: #4f4f4f;
}
.products {
  grid-column: 2 / 4;
  grid-row: 2 / 4;
}
.products__sort-wrapper {
  margin-bottom: 16px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.products__pagination {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  column-gap: 16px;
  width: 500px;
}
.products__pagination-link {
  text-decoration: none;
  color: #000000;
  font-size: 16px;
  line-height: 19px;
  display: block;
  text-align: center;
  padding-top: 8px;
  box-sizing: border-box;
  width: 35px;
  height: 35px;
  background-color: #ffffff;
}
.products__pagination-link--current {
  background-color: #56ccf2;
}
.products__pagination-link.disabled {
  color: #bdbdbd;
}
.products__options-text {
  font-size: 16px;
  line-height: 19px;
  color: #000000;
}
.products__options-select {
  width: 210px;
  height: 35px;
  border: 1px solid #bdbdbd;
  padding: 0 10px;
  color: #828282;
}
.products__list,
.products__description-list,
.products__parameters-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
.products__item {
  background-color: #ffffff;
  display: grid;
  padding: 16px;
  grid-template-columns: 224px auto 100px;
  grid-template-rows: repeat(2, auto) 26px;
  row-gap: 5px;
  column-gap: 16px;
  margin-bottom: 16px;
  min-height: 200px;
  box-sizing: border-box;
}
.products__image {
  grid-column: 1 / 2;
  grid-row: 1 / -1;
}
.products__title {
  margin: 0;
  font-weight: 500;
  font-size: 18px;
  line-height: 21px;
  color: #000000;
  grid-column: 2 / 3;
}
.products__price {
  margin: 0;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  text-align: right;
  color: #4f4f4f;
}
.products__description-list {
  grid-column: 2 / 3;
  min-height: 83px;
}
.products__description-item {
  width: 180px;
  font-weight: 300;
  line-height: 16px;
  color: #828282;
  margin-right: 10px;
  display: inline-block;
}
.products__parameters-list {
  grid-column: 2 / 3;
  display: flex;
  gap: 16px;
}
.products__parameters-item {
  background-color: #c4c4c4;
  font-weight: 300;
  font-size: 12px;
  line-height: 14px;
  color: #ffffff;
  padding: 5px 10px;
}
.page-text {
  grid-row: -2 / -1;
  grid-column: 2 / 3;
}
.page-text__item {
  font-size: 16px;
  line-height: 20px;
  color: #4f4f4f;
}
